const { Title } = Typography;

const BingoTable = (card: BingoCard) => {
  const [data, setData] = useState(card.goals);

  console.log(card)

  // Toggle completion status of a goal
  const toggleCompletion = (id) => {
    const updatedData = data.map((goal) =>
      goal.id === id ? { ...goal, isCompleted: !goal.isCompleted } : goal
    );
    setData(updatedData);
  };

  // Create a grid with 5x5 rows and columns
  const columns = Array(card.size)
    .fill(null)
    .map((_, rowIndex) => ({
      title: `Column ${rowIndex + 1}`,
      dataIndex: `col-${rowIndex}`,
      key: rowIndex,
      render: (_, record) => {
        const goal = record[`col-${rowIndex}`] as Goal;
        return (
          <Button
            
            style={{ width: '100%' }}
          >{goal?.title}
          </Button>
        );
      },
    }));

  const dataSource = Array(card.size)
    .fill(null)
    .map((_, rowIndex) => {
      const rowData = {};
      for (let i = 0; i < card.size; i++) {
        const index = rowIndex * card.size + i;
        if (card.goals[index]) {
          rowData[`col-${i}`] = card.goals[index];
        }
      }
      return rowData;
    });

  return (
    <div>
        
      <Title>{card.title}</Title>
      <Table
        dataSource={dataSource}
        columns={columns}
        pagination={false}
        bordered
        rowKey={(record, index) => index}
        showHeader={false}
        style={{ width: '100%' }}
      />
    </div>
  );
};

export default BingoTable;